<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div
    v-if="productStatus.isLoading"
    class="content container"
  >
    <BaseLoader
      :title="'Загрузка товаров...'"
      style="position:relative"
    />
  </div>

  <div
    v-else-if="productStatus.isFailed"
    class="content container"
  >
    Не удалось загрузить товар
  </div>

  <main
    v-else
    class="content container"
  >
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{name:'main'}"
          >
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{name:'category', params: {id:currentProductData.category}}"
          >
            {{ product.category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a
            class="breadcrumbs__link"
          >
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :src="currentProductData.image"
            :alt="product.title"
          >
        </div>
        <ul class="pics__list">
          <template
            v-for="img in product.colors"
            :key="img.id"
          >
            <li
              v-if="img.id !== product.curColorId"
              class="pics__item"
            >
              <a
                class="pics__link"
                @click="changeColor(img.id)"
                @keydown="changeColor(img.id)"
              >
                <img
                  width="98"
                  height="98"
                  :src="img.gallery[0].file.url"
                  alt="Название товара"
                >
              </a>
            </li>
          </template>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h3 class="item__title">
          {{ product.title }}
        </h3>
        <div class="item__form">
          <form
            class="form"
            action="#"
            method="POST"
            @submit.prevent="doAddToCart"
          >
            <div class="item__row item__row--center">
              <ProductCounter
                v-model:count="currentProductData.amount"
              />

              <b class="item__price">
                {{ product.pricePretty }} ₽
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">
                  Цвет
                </legend>
                <ul class="colors colors--black">
                  <li
                    v-for="color in product.colors"
                    :key="color.id"
                    class="colors__item"
                  >
                    <label
                      class="colors__label"
                    >
                      <input
                        :key="color.id"
                        class="colors__radio sr-only"
                        type="radio"
                        name="color"
                        :value="product.id"
                        :checked="product.curColorId == color.id ? true : false"
                        @change.prevent="changeColor(color.id)"
                      >
                      <span
                        class="colors__value"
                        :style="{backgroundColor: color.color.code}"
                      />
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">
                  Размер
                </legend>
                <label
                  class="form__label form__label--small form__label--select"
                  for="category"
                >
                  <select
                    v-model="currentProductData.size"
                    class="form__select"
                    name="category"
                  >
                    <option
                      v-for="size in product.sizes"
                      :key="size.id"
                      :value="size.id"
                    >
                      {{ size.title }}
                    </option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button
              class="item__button button"
              :class="{ 'button--secondary': isInCart, 'button--primery': !isInCart}"
              type="submit"
            >
              {{ isInCart ? 'В корзине' : 'В корзину' }}
            </button>

            <BaseModal
              v-if="isAddedToCart"
              v-model:open="isShowAddedMessage"
            >
              {{ 'Товар добавлен в корзину' }}
            </BaseModal>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ProductTabs>
          <ProductTab
            title="Информация о товаре"
            tab-key="my-tab-1"
          >
            <div class="item__content">
              {{ description }}
            </div>
          </ProductTab>
          <ProductTab
            title="Доставка и возврат"
            tab-key="my-tab-2"
          >
            <div class="item__content">
              <h3>Доставка:</h3>

              <ul>
                <li
                  v-for="del in deliveries"
                  :key="del.id"
                >
                  <p>
                    {{ del.id }}.  {{ del.title }}. Цена - {{ del.price }} ₽
                  </p>
                </li>
              </ul>

              <h3>Возврат:</h3>

              <p>
                Любой возврат должен быть осуществлен в течение Возвраты в магазине БЕСПЛАТНО.<br>
                Вы можете вернуть товары в любой магазин. Магазин должен быть расположен в стране,
                в которой Вы осуществили покупку.
                БЕСПЛАТНЫЙ возврат в Пункт выдачи заказов.<br>
                Для того чтобы вернуть товар в одном из наших Пунктов выдачи заказов,
                позвоните по телефону 8 800 600 90 09<br>
              </p>
            </div>
          </ProductTab>
        </ProductTabs>
      </div>
    </section>
  </main>
</template>

<script>

// import { useStore } from 'vuex';
import {
  defineComponent, ref, computed, watch, reactive,
} from 'vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';

import BaseLoader from '@/components/base/BaseLoader.vue';
import ProductCounter from '@/components/product/ProductCounter.vue';
import ProductTabs from '@/components/product/ProductTabs.vue';
import ProductTab from '@/components/product/ProductTab.vue';
import BaseModal from '@/components/base/BaseModal.vue';

import useProduct from '@/hooks/useProduct';
import useCart from '@/hooks/useCart';

export default defineComponent({
  components: {
    BaseLoader, ProductCounter, ProductTabs, ProductTab, BaseModal,
  },

  setup() {
    const $route = useRoute();
    const store = useStore();
    const currentProductData = reactive({
      id: null, amount: null, color: null, size: null, image: null, category: null,
    });

    const { product, fetchProduct, status: productStatus } = useProduct();
    const { addToCart, status: addStatus } = useCart();

    const isShowAddedMessage = ref(false);

    fetchProduct($route.params.id);

    const getColorData = (id) => {
      const currentColor = product.value.colors.find(
        (color) => color.id === id,
      );
      const { gallery } = currentColor;
      currentProductData.color = currentColor.color.id;
      currentProductData.image = gallery[0].file.url;
    };

    watch([product], () => {
      currentProductData.id = product.value.id;
      currentProductData.amount = 1;
      currentProductData.size = product.value.sizes[0].id;
      currentProductData.category = product.value.category.id;
      getColorData(product.value.curColorId);
    });

    const changeColor = (id) => {
      product.value.curColorId = id;
      getColorData(id);
    };

    const doAddToCart = () => {
      addToCart(currentProductData);
      isShowAddedMessage.value = true;
    };

    const isInCart = computed(() => store.state.cartProducts.findIndex(
      (item) => item.productId === currentProductData.id
      && item.colorId === currentProductData.color && item.sizeId === currentProductData.size,
    ) !== -1);

    return {
      product,
      currentProductData,

      deliveries: computed(() => store.getters.deliveriesList),
      description: computed(() => (product.value.content === '' ? 'Пока здесь ничего нет' : product.value.content)),

      productStatus,
      isAddedToCart: computed(() => addStatus.isAdded),
      isShowAddedMessage,

      changeColor,
      doAddToCart,

      isInCart,
    };
  },
});

</script>

<style scoped>
  .sr-only:checked + span::before {
      border-color: var(--border-color);
    }
  .colors__value {
    box-shadow: 0 0 10px rgba(0,0,0,0.15);
  }

  ul {
    padding: 0;
  }

  li {
    list-style-type: none;
  }

  p {
    margin: 0;
  }

  .tabs__link {
    cursor: pointer;
  }

  .button--secondary {
    background-color: aquamarine;
  }
</style>
