import {
  computed, getCurrentInstance, ref, watch,
} from 'vue';

const openModalsCount = ref(new Set());

export default function getModal() {
  const { uid } = getCurrentInstance();
  const isOpen = ref(false);
  const padding = ref(0);

  const doOpen = () => {
    isOpen.value = true;
    padding.value = `${window.innerWidth - document.documentElement.clientWidth}px`;
    openModalsCount.value.add(uid);
  };

  const doClose = () => {
    isOpen.value = false;
    openModalsCount.value.delete(uid);
  };

  const isSomeOpen = computed(() => !!openModalsCount.value.size);

  const checkBlackoutState = () => {
    if (!isSomeOpen.value) {
      document.body.style.overflow = null;
      document.body.style.paddingRight = null;
    } else {
      // eslint-disable-next-line max-len
      document.body.style.paddingRight = padding.value;
      document.body.style.overflow = 'hidden';
    }
  };

  watch(isSomeOpen, () => {
    checkBlackoutState();
  });

  return {
    doOpen,
    doClose,

    isOpen,
    isSomeOpen,

    padding,
  };
}
