<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <BaseFormField
    :title="title"
    :error="error"
  >
    <input
      v-if="isTel"
      v-model="dataValue"
      v-maska
      data-maska="+7 (###) ###-##-##"
      class="form__input"
      :type="type"
      :placeholder="placeholder"
    >
    <input
      v-else
      v-model="dataValue"
      class="form__input"
      :type="type"
      :placeholder="placeholder"
    >
  </BaseFormField>
</template>

<script >
import {
  defineComponent, ref,
} from 'vue';
import formFieldMixin from '@/mixins/formFieldMixin';
import { vMaska } from 'maska';

export default defineComponent({
  directives: { maska: vMaska },
  mixins: [formFieldMixin],
  props: {
    type: {
      type: String,
      default: 'text',
    },
  },

  setup(props) {
    const isTel = ref(false);
    if (props.type === 'tel') {
      isTel.value = true;
    }

    return {
      isTel,
    };
  },
});

</script>
