<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="product.image"
        width="120"
        height="120"
        srcset="img/product-square-4@2x.jpg 2x"
        alt="Название товара"
      >
    </div>
    <h3 class="product__title">
      {{ product.title }}
    </h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i
          :style="{backgroundColor: product.colorCode}"
          class="product__color"
        />
        {{ colorRu }}

        Размер:
        {{ product.size }}
      </span>
    </p>

    <span class="product__code">
      Артикул: {{ product.productId }}
    </span>

    <ProductCounter
      v-model:count="amount"
    />

    <b class="product__price">
      {{ totalPrice }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct()"
    >
      <svg
        width="20"
        height="20"
        fill="currentColor"
      >
        <use xlink:href="#icon-close" />
      </svg>
    </button>
  </li>
</template>

<script>
import { computed, defineComponent } from 'vue';
import { useStore } from 'vuex';
import ruColor from '@/helpers/ruColor';
import numberFormat from '@/helpers/numberFormat';
import ProductCounter from '@/components/product/ProductCounter.vue';

export default defineComponent({
  components: { ProductCounter },
  props: { product: { type: Object, default() { return {}; } } },

  setup(props) {
    const store = useStore();
    const colorRu = computed(() => ruColor(props.product.colorName));

    const amount = computed({
      get() {
        return props.product.amount;
      },
      set(value) {
        store.dispatch('updateCartProductAmount', { cartId: props.product.cartId, amount: value });
      },
    });

    const totalPrice = computed(() => numberFormat(amount.value * props.product.price));
    const deleteProduct = () => {
      store.dispatch('deleteProductFromCart', { cartId: props.product.cartId });
    };

    return {
      colorRu,
      amount,
      totalPrice,
      deleteProduct,
    };
  },
});
</script>

<style scoped>
  .product__color {
    box-shadow: 0 0 10px rgba(0,0,0,0.15);
  }
  .button-del {
    cursor: pointer;
  }
</style>
