import { createStore } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default createStore({
  state: {
    userAccessKey: null,
    deliveriesList: [],
    paymentsList: [],
    cartProducts: [],
    cartProductsData: [],
    cartLoading: false,
    orderInfo: null,
  },
  getters: {
    deliveriesList(state) {
      return state.deliveriesList;
    },
    paymentsList(state) {
      return state.paymentsList;
    },
    cartLoading(state) {
      return state.cartLoading;
    },
    cartTotalPrice(state) {
      return state.cartProducts.reduce(
        (acc, item) => (item.price * item.amount) + acc,
        0,
      );
    },
    cartPositionsAmount(state) {
      return state.cartProducts.length;
    },
    cartTotalAmount(state) {
      return state.cartProducts.reduce(
        (acc, item) => item.amount + acc,
        0,
      );
    },
    getOrderBasket(state) {
      let basket = null;

      if (state.orderInfo) {
        basket = state.orderInfo.basket.items.map((item) => ({
          ...item.product,
        }));
      }
      return basket;
    },
  },
  mutations: {
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateDeliveriesList(state, items) {
      state.deliveriesList = items;
    },
    updatePaymentsList(state, items) {
      state.paymentsList = items;
    },
    updateCartLoading(state, params) {
      state.cartLoading = params;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    updateCartProductAmount(state, { cartId, amount }) {
      const itemInCart = state.cartProducts.find((item) => item.cartId === cartId);

      if (itemInCart) {
        itemInCart.amount = amount;
      }
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        cartId: item.id,
        amount: item.quantity,
        price: item.price,
        colorCode: item.color.color.code,
        colorId: item.color.color.id,
        colorName: item.color.color.title,
        image: item.color.gallery[0].file.url,
        size: item.size.title,
        sizeId: item.size.id,
        title: item.product.title,
      }));
    },
    deleteCartProduct(state, cartId) {
      state.cartProducts = state.cartProducts.filter((item) => item.cartId !== cartId);
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
  },
  actions: {
    loadCart(context) {
      context.commit('updateCartLoading', true);
      setTimeout(() => axios.get(`${API_BASE_URL}/api/baskets`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
          context.commit('updateCartLoading', false);
        }), 100);
    },

    loadDeliveries(context) {
      return axios
        .get(`${API_BASE_URL}/api/deliveries`)
        .then((response) => {
          context.commit('updateDeliveriesList', response.data);
        });
    },

    loadPaymentList(context, { id }) {
      return axios
        .get(`${API_BASE_URL}/api/payments?deliveryTypeId=${id}`)
        .then((response) => {
          context.commit('updatePaymentsList', response.data);
        });
    },

    addProductToCart(context, {
      productId, amount, colorId, sizeId,
    }) {
      context.commit('updateCartLoading', true);
      // eslint-disable-next-line no-promise-executor-return
      return (new Promise((resolve) => setTimeout(resolve, 100)))
        .then(() => axios
          .post(`${API_BASE_URL}/api/baskets/products`, {
            productId,
            quantity: amount,
            colorId,
            sizeId,
          }, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
          .then((response) => {
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
            context.commit('updateCartLoading', false);
          }));
    },
    deleteProductFromCart(context, { cartId }) {
      context.commit('deleteCartProduct', cartId);

      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
          data: {
            basketItemId: cartId,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    updateCartProductAmount(context, { cartId, amount }) {
      context.commit('updateCartProductAmount', { cartId, amount });

      if (amount < 1) { return; }
      // eslint-disable-next-line consistent-return
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          basketItemId: cartId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    loadOrderInfo(context, orderId) {
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
  },
  modules: {
  },
});
