/* eslint-disable no-return-assign */
import { ref, reactive } from 'vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import numberFormat from '@/helpers/numberFormat';

export default function getProductsList() {
  const productsData = ref(null);

  const fetchStatus = reactive({
    isLoading: false,
    isFailed: false,
  });

  const totalAmount = ref(0);
  const fetchProducts = ({
    page, limit, categoryId, minPrice, maxPrice, colorIds, materialIds, seasonIds,
  }) => {
    fetchStatus.isLoading = true;
    fetchStatus.isFailed = false;

    let loadProductsTimer;
    clearTimeout(loadProductsTimer);
    loadProductsTimer = setTimeout(() => {
      axios.get(`${API_BASE_URL}/api/products`, {
        params: {
          page,
          limit,
          categoryId,
          minPrice,
          maxPrice,
          colorIds,
          materialIds,
          seasonIds,
        },
      })
      // eslint-disable-next-line no-return-assign
        .then((response) => {
          const { data } = response;
          totalAmount.value = data.pagination.total;
          // productsData.value = Object.assign(data.items);
          productsData.value = data.items.map((item) => ({
            ...item,
            pricePretty: numberFormat(item.price),
            curColorId: item.colors[0].id,
            colors: item.colors.map((color) => ({
              ...color,
              gallery: color.gallery === null ? [{ file: { url: 'img/default.png' } }] : color.gallery,
            })),
          }));
        })
      // eslint-disable-next-line no-return-assign
        .catch(() => { fetchStatus.isFailed = true; })
      // eslint-disable-next-line no-return-assign
        .then(() => { fetchStatus.isLoading = false; });
    }, 0);
  };

  return {
    products: productsData,
    totalAmount,
    status: fetchStatus,

    fetchProducts,
  };
}
