<template>
  <main
    v-if="info"
    class="content container"
  >
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{name:'main'}"
          >
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Данные заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ info.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
      >
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина.
            На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <OrderInfoItem
              v-if="info"
              title="Получатель"
              :value="info.name"
            />
            <OrderInfoItem
              v-if="info"
              title="Адрес доставки"
              :value="info.address"
            />
            <OrderInfoItem
              v-if="info"
              title="Телефон"
              :value="info.phone"
            />
            <OrderInfoItem
              v-if="info"
              title="Email"
              :value="info.email"
            />
            <OrderInfoItem
              title="Способ оплаты"
              :value="info.paymentType"
            />

            <OrderInfoItem
              title="Комментарий"
              :value="info.comment"
            />
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem
              v-for="product in basket"
              :key="product.id"
              :product="product"
            />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b> {{ deliveryPrice == 0 ? 'Бесплатно' : deliveryPrice + ' ₽' }}</b></p>
            <p>
              Итого:
              <b> {{ totalAmount }}</b>
              {{ textToAmount }} на сумму
              <b>{{ totalPrice }} ₽</b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import {
  defineComponent, computed,
} from 'vue';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';

import OrderInfoItem from '@/components/order/OrderInfoItem.vue';
import OrderItem from '@/components/order/OrderItem.vue';

import numberFormat from '@/helpers/numberFormat';
import wordEnding from '@/helpers/wordEnding';

export default defineComponent({
  components: { OrderInfoItem, OrderItem },

  setup() {
    const store = useStore();
    const route = useRoute();

    store.dispatch('loadOrderInfo', route.params.id);

    const info = computed(() => store.state.orderInfo) || null;
    const basket = computed(() => store.getters.getOrderBasket);
    const totalAmount = computed(() => numberFormat(basket.value.length));

    return {
      info,
      basket,
      deliveryPrice: computed(() => numberFormat(info.value.deliveryType.price)),
      totalAmount,
      textToAmount: computed(() => wordEnding(totalAmount.value, ['товар', 'товара', 'товаров'])),
      totalPrice: computed(() => numberFormat(info.value.totalPrice)),
    };
  },
});

</script>
