<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{name:'main'}"
          >
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{name:'cart'}"
          >
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              title="ФИО"
              :error="formError.name"
              placeholder="Введите ваше полное имя"
            />

            <BaseFormText
              v-model="formData.address"
              title="Адрес доставки"
              :error="formError.address"
              placeholder="Введите ваш адрес"
            />

            <BaseFormText
              v-model="formData.phone"
              title="Телефон"
              type="tel"
              :error="formError.phone"
              placeholder="+7 (***) *** ** **"
            />

            <BaseFormText
              v-model="formData.email"
              title="Email"
              type="email"
              :error="formError.email"
              placeholder="Введите ваш Email"
            />

            <BaseFormTextarea
              v-model="formData.comment"
              title="Комментарий к заказу"
              :error="formError.comment"
              placeholder="Ваши пожелания"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">
              Доставка
            </h3>
            <ul class="cart__options options">
              <li
                v-for="delivery in deliveries"
                :key="delivery.title"
                class="options__item"
              >
                <label class="options__label">
                  <input
                    v-model="currentDelivery"
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    :value="delivery.id"
                    :checked="delivery.id === formData.deliveryTypeId"
                  >
                  <span class="options__value">
                    {{ delivery.title }} <b>{{ delivery.price }}</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">
              Оплата
            </h3>
            <ul class="cart__options options">
              <li
                v-for="payment in payments"
                :key="payment.title"
                class="options__item"
              >
                <label class="options__label">
                  <input
                    v-model="formData.paymentTypeId"
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    :value="payment.id"
                    :checked="payment.id === formData.paymentTypeId"
                  >
                  <span class="options__value">
                    {{ payment.title }}
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem
              v-for="product in products"
              :key="product.productId"
              :product="product"
            />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b> {{ deliveryPrice == 0 ? 'Бесплатно' : deliveryPrice + ' ₽' }}</b></p>
            <p>
              Итого:
              <b> {{ totalAmount }}</b> {{ textToAmount }} на сумму <b>{{ totalPrice }} ₽</b>
            </p>
          </div>

          <button
            v-if="totalAmount > 0"
            class="cart__button button button--primery"
            type="submit"
          >
            Оформить заказ
          </button>
        </div>

        <BaseLoader
          v-if="loading"
          style="position:relative"
          :title="'Оформление заказа...'"
        />
        <div
          v-if="formErrorMessage"
          class="cart__error form__error-block"
        >
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import {
  defineComponent, ref, reactive, computed, watch,
} from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import numberFormat from '@/helpers/numberFormat';
import wordEnding from '@/helpers/wordEnding';
import BaseFormText from '@/components/base/BaseFormText.vue';
import BaseFormTextarea from '@/components/base/BaseFormTextarea.vue';
import OrderItem from '@/components/order/OrderItem.vue';
import BaseLoader from '@/components/base/BaseLoader.vue';

import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default defineComponent({
  components: {
    BaseFormText, BaseFormTextarea, OrderItem, BaseLoader,
  },

  setup() {
    const router = useRouter();
    const store = useStore();
    const formData = reactive({
      name: null,
      address: null,
      phone: null,
      email: null,
      deliveryTypeId: 1,
      paymentTypeId: 1,
      comment: '',
    });
    const formError = reactive({
      name: null,
      address: null,
      phone: null,
      email: null,
    });
    const formErrorMessage = ref('');
    const loading = ref(false);

    const currentDelivery = ref(formData.deliveryTypeId);
    const deliveries = computed(() => store.getters.deliveriesList.map((item) => ({
      id: item.id,
      title: item.title,
      price: numberFormat(item.price),
    })));
    const deliveryPrice = computed(() => {
      const price = deliveries.value.find(
        (item) => item.id === formData.deliveryTypeId,
      );
      return price ? price.price : 0;
    });

    const products = computed(() => store.state.cartProducts);

    store.dispatch('loadPaymentList', { id: formData.deliveryTypeId });
    const payments = computed(() => store.getters.paymentsList);

    watch([currentDelivery], () => {
      formData.deliveryTypeId = currentDelivery.value;
      store.dispatch('loadPaymentList', { id: formData.deliveryTypeId });
    });

    watch([payments], () => {
      formData.paymentTypeId = payments.value[0].id;
    });

    const totalAmount = computed(() => numberFormat(store.getters.cartPositionsAmount));
    const totalPrice = computed(() => numberFormat(store.getters.cartTotalPrice));
    const textToAmount = computed(() => wordEnding(totalAmount.value, ['товар', 'товара', 'товаров']));

    const isValid = ref(false);

    const validPhone = (number) => {
      const re = /^((\+7)[- ]?)?(\(?\d{3}\)?[- ]?)?[\d\- ]{10}$/;
      return re.test(number);
    };

    const order = () => {
      isValid.value = true;
      formErrorMessage.value = '';
      Object.keys(formError).forEach((key) => {
        formError[key] = null;
      });

      Object.keys(formData).forEach((key) => {
        if (formData[key] == null) {
          formError[key] = 'Поле обязательно для заполнения';
          isValid.value = false;
          formErrorMessage.value = 'Заполнены не все обязательные поля';
        }

        if (key === 'phone') {
          if (!validPhone(formData[key])) {
            isValid.value = false;
            formError[key] = 'Некорректный номер телефона';
            formErrorMessage.value = 'Ошибки в заполнении формы';
          }
        }
      });

      if (!isValid.value) {
        return;
      }

      loading.value = true;

      axios
        .post(`${API_BASE_URL}/api/orders`, {
          ...formData,
        }, {
          params: {
            userAccessKey: store.state.userAccessKey,
          },
        })
        .then((response) => {
          store.commit('resetCart');
          store.commit('updateOrderInfo', response.data);
          router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          Object.keys(error.response.data.error.request).forEach((key) => {
            formError[key] = error.response.data.error.request[key];
          });
          formErrorMessage.value = error.response.data.error.message || 'Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите страницу.';
        })
        .then(() => {
          loading.value = false;
        });
    };

    return {
      deliveries,
      payments,
      formData,
      formError,
      formErrorMessage,
      order,
      currentDelivery,
      deliveryPrice,
      products,
      totalAmount,
      totalPrice,
      textToAmount,
      loading,
    };
  },
});
</script>
