<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{name:'main'}"
          >
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{ positionsAmount }} {{ textToAmount }}
        </span>
      </div>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
      >
        <div class="cart__field">
          <BaseLoader
            v-if="loading"
            style="position:relative"
            :title="'Загрузка товаров...'"
          />
          <ul class="cart__list">
            <CartItem
              v-for="product in products"
              :key="product.productId"
              :product="product"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice }} ₽</span>
          </p>

          <router-link
            v-slot="{navigate}"
            :to="{name: 'order'}"
            custom
          >
            <button
              class="cart__button button button--primery"
              :disabled="disabled"
              type="button"
              @click="navigate"
            >
              Оформить заказ
            </button>
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { defineComponent, computed } from 'vue';
import { useStore } from 'vuex';
import wordEnding from '@/helpers/wordEnding';
import numberFormat from '@/helpers/numberFormat';
import BaseLoader from '@/components/base/BaseLoader.vue';
import CartItem from '@/components/cart/CartItem.vue';

export default defineComponent({
  components: { BaseLoader, CartItem },
  setup() {
    const store = useStore();
    const positionsAmount = computed(() => store.getters.cartPositionsAmount);
    const totalPrice = computed(() => numberFormat(store.getters.cartTotalPrice));
    const loading = computed(() => store.getters.cartLoading);
    const products = computed(() => store.state.cartProducts);
    const textToAmount = computed(() => wordEnding(positionsAmount, ['товар', 'товара', 'товаров']));
    const disabled = computed(() => !positionsAmount.value > 0);

    return {
      positionsAmount,
      totalPrice,
      textToAmount,
      loading,
      products,
      disabled,
    };
  },
});

</script>
