<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <li class="catalog__item">
    <a
      class="catalog__pic"
      href="#"
    >
      <router-link :to="{name: 'product', params: {id: product.id}}">
        <img
          :id="product.id"
          :src="currentImg"
          :alt="product.title"
        >
      </router-link>
    </a>

    <h3 class="catalog__title">
      <a
        href="#"
        @click.prevent="openQuickView(product.id)"
      >
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.pricePretty }} ₽
    </span>

    <ul class="colors colors--black">
      <li
        v-for="color in product.colors"
        :key="color.id"
        class="colors__item"
      >
        <label
          class="colors__label"
        >
          <input
            :key="color.id"
            class="colors__radio sr-only"
            type="radio"
            name="color"
            :value="product.id"
            @change.prevent="changeColor(color.id)"
          >
          <span
            class="colors__value"
            :style="{backgroundColor: color.color.code}"
          />
        </label>
      </li>
    </ul>
  </li>
  <BaseModal v-model:open="isQuickViewOpen">
    <ProductQuickView :product-id="currentProductId" />
  </BaseModal>
</template>

<script>

import {
  computed, defineComponent, ref, defineAsyncComponent, h,
} from 'vue';
import BaseModal from '@/components/base/BaseModal.vue';

export default defineComponent({
  components: {
    BaseModal,
    ProductQuickView: defineAsyncComponent({
      loader: () => import('@/components/product/ProductQuickView.vue'),
      delay: 0,
      loadingComponent: () => h('div', 'Загрузка...'),
    }),
  },
  inheritAttrs: false,
  props: { product: { type: Object, default() { return {}; } } },

  setup(props) {
    const currentProductId = ref(null);
    const currentColorId = ref(props.product.curColorId);

    const currentImg = computed(() => {
      const images = props.product.colors.find(
        (color) => color.id === currentColorId.value,
      ).gallery;
      return images[0].file.url;
    });

    const changeColor = (id) => {
      currentColorId.value = id;
    };

    const isQuickViewOpen = computed({
      get() {
        return !!currentProductId.value;
      },
      set(isOpen) {
        if (!isOpen) {
          currentProductId.value = null;
        }
      },
    });

    const openQuickView = (productId) => {
      currentProductId.value = productId;
    };

    return {
      currentImg,
      changeColor,
      isQuickViewOpen,
      openQuickView,
      currentProductId,
    };
  },

});

</script>

<style scoped>
  .sr-only:checked + span::before {
      border-color: var(--border-color);
    }
  .colors__value {
    box-shadow: 0 0 10px rgba(0,0,0,0.15);
  }
</style>
