<!-- eslint-disable vue/no-mutating-props -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <h2 class="filter__title">
    Фильтры
  </h2>

  <form
    v-bind="$attrs"
    class="filter__form form"
    action="#"
    method="get"
    @submit.prevent="submit"
  >
    <fieldset class="form__block">
      <legend class="form__legend">
        Цена
      </legend>
      <label class="form__label form__label--price">
        <input
          v-model.number="currentPriceFrom"
          class="form__input"
          type="text"
          name="min-price"
        >
        <span class="form__value">От</span>
      </label>
      <label class="form__label form__label--price">
        <input
          v-model.number="currentPriceTo"
          class="form__input"
          type="text"
          name="max-price"
        >
        <span class="form__value">До</span>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">
        Категория
      </legend>
      <label
        class="form__label form__label--select"
        for="category"
      >
        <select
          v-model="currentCategoryId"
          class="form__select"
          name="category"
        >
          <option value="0">Все категории</option>
          <option
            v-for="category in categories"
            :key="category.id"
            :value="category.id"
          >
            {{ category.title }}
          </option>
        </select>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">
        Цвет
      </legend>
      <ul class="colors colors--black">
        <li
          v-for="color in colors"
          :key="color.id"
          class="colors__item"
        >
          <label class="colors__label">
            <input
              v-model="currentColorId"
              class="sr-only"
              type="checkbox"
              name="color"
              :value="color.id"
              checked=""
            >
            <span
              class="colors__value"
              :style="{backgroundColor: color.code}"
            />
          </label>
        </li>
      </ul>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">
        Материал
      </legend>
      <ul class="check-list">
        <li
          v-for="material in materials"
          :key="material.id"
          class="check-list__item"
        >
          <label class="check-list__label">
            <input
              v-model="currentMaterialId"
              class="check-list__check sr-only"
              type="checkbox"
              name="material"
              :value="material.id"
              checked=""
            >
            <span class="check-list__desc">
              {{ material.title }}
              <span>( {{ material.productsCount }} )</span>
            </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">
        Коллекция
      </legend>
      <ul class="check-list">
        <li
          v-for="season in seasons"
          :key="season.id"
          class="check-list__item"
        >
          <label class="check-list__label">
            <input
              v-model="currentSeasonId"
              class="check-list__check sr-only"
              type="checkbox"
              name="season"
              :value="season.id"
              checked=""
            >
            <span class="check-list__desc">
              {{ season.title }}
              <span>( {{ season.productsCount }} )</span>
            </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <button
      class="filter__submit button button--primery"
      type="submit"
    >
      Применить
    </button>
    <button
      v-if="currentPriceFrom > 0 ||
        currentPriceTo > 0 ||
        currentCategoryId > 0 ||
        currentColorId.length > 0 ||
        currentMaterialId.length > 0 ||
        currentSeasonId.length > 0"

      class="filter__reset button button--second"
      type="button"
      @click.prevent="reset"
    >
      Сбросить
    </button>
  </form>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import {
  defineComponent, ref,
} from 'vue';
import { useRouter } from 'vue-router';

export default defineComponent({
  props: {
    priceFrom: { type: Number, default: 0 },
    priceTo: { type: Number, default: 0 },
    categoryId: { type: Number, default: 0 },
    colorId: { type: Array, default() { return []; } },
    materialId: { type: Array, default() { return []; } },
    seasonId: { type: Array, default() { return []; } },
  },

  emits: ['update:priceFrom', 'update:priceTo',
    'update:categoryId', 'update:colorId', 'update:materialId', 'update:seasonId'],

  setup(props, { emit }) {
    const $router = useRouter();
    const categories = ref(0);
    const colors = ref(0);
    const materials = ref(0);
    const seasons = ref(0);

    const currentPriceFrom = ref(0);
    const currentPriceTo = ref(0);
    const currentCategoryId = ref(props.categoryId);
    const currentColorId = ref([]);
    const currentMaterialId = ref([]);
    const currentSeasonId = ref([]);

    const loadCategories = () => {
      axios.get(`${API_BASE_URL}/api/productCategories`)
      // eslint-disable-next-line no-return-assign
        .then((response) => categories.value = response.data.items);
    };

    const loadColors = () => {
      axios.get(`${API_BASE_URL}/api/colors`)
      // eslint-disable-next-line no-return-assign
        .then((response) => colors.value = response.data.items);
    };

    const loadMaterials = () => {
      axios.get(`${API_BASE_URL}/api/materials`)
      // eslint-disable-next-line no-return-assign
        .then((response) => materials.value = response.data.items);
    };

    const loadSeasons = () => {
      axios.get(`${API_BASE_URL}/api/seasons`)
      // eslint-disable-next-line no-return-assign
        .then((response) => seasons.value = response.data.items);
    };

    loadCategories();
    loadColors();
    loadMaterials();
    loadSeasons();

    const emitData = () => {
      emit('update:priceFrom', currentPriceFrom.value);
      emit('update:priceTo', currentPriceTo.value);
      emit('update:categoryId', currentCategoryId.value);
      emit('update:colorId', currentColorId.value);
      emit('update:materialId', currentMaterialId.value);
      emit('update:seasonId', currentSeasonId.value);
    };

    const submit = () => {
      emitData();
    };

    const reset = () => {
      currentPriceFrom.value = 0;
      currentPriceTo.value = 0;
      currentCategoryId.value = 0;
      currentColorId.value = [];
      currentMaterialId.value = [];
      currentSeasonId.value = [];
      $router.push('/');
      emitData();
    };

    return {
      categories,
      colors,
      materials,
      seasons,

      currentPriceFrom,
      currentPriceTo,
      currentCategoryId,
      currentColorId,
      currentMaterialId,
      currentSeasonId,

      submit,
      reset,

    };
  },
});

</script>

<style scoped>
  .sr-only:checked + span::before {
    border-color: var(--border-color);
  }

  .colors__value {
  box-shadow: 0 0 10px rgba(0,0,0,0.15);
}
</style>
