/* eslint-disable no-return-assign */
import { ref, reactive } from 'vue';
import axios from 'axios';
import numberFormat from '@/helpers/numberFormat';
import { API_BASE_URL } from '@/config';

export default function getProduct() {
  const productData = ref(null);

  const fetchStatus = reactive({
    isLoading: false,
    isFailed: false,
  });

  const fetchProduct = (productId) => {
    fetchStatus.isLoading = true;
    fetchStatus.isFailed = false;
    setTimeout(() => {
      axios.get(`${API_BASE_URL}/api/products/${productId}`)
        .then((response) => {
          const product = response.data;
          productData.value = Object.assign(product, {
            pricePretty: numberFormat(product.price),
            curColorId: product.colors[0].id,
            colors: product.colors.map((color) => ({
              ...color,
              gallery: color.gallery === null ? [{ file: { url: 'img/default.png' } }] : color.gallery,
            })),
          });
        })
        .catch(() => {
          fetchStatus.isFailed = true;
        })
        .then(() => fetchStatus.isLoading = false);
    }, 0);
  };

  return {
    product: productData,
    status: fetchStatus,

    fetchProduct,
  };
}
