<template>
  <main class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{ totalAmount }} {{ textToAmount }}
        </span>
      </div>
    </div>

    <div class="content__catalog">
      <aside class="filter">
        <h3 class="catalog__title">
          Количество товаров на странице
        </h3>
        <ProductCounter
          v-model:count="productsPerPage"
        />
        <ProductFilter
          v-model:price-from="filterPriceFrom"
          v-model:price-to="filterPriceTo"
          v-model:category-id="filterCategoryId"
          v-model:color-id="filterColorId"
          v-model:material-id="filterMaterialId"
          v-model:season-id="filterSeasonId"
          class="filter-test"
        />
      </aside>

      <div v-if="productStatus.isLoading">
        <BaseLoader
          :title="'Загрузка товаров...'"
          style="position:relative"
        />
      </div>

      <section class="catalog">
        <ProductList
          :products="products"
        />

        <BasePagination
          v-model:current-page="page"
          :count="totalAmount"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import {
  defineComponent, ref, watch, computed,
} from 'vue';
import { useRoute } from 'vue-router';
import ProductFilter from '@/components/product/ProductFilter.vue';
import ProductList from '@/components/product/ProductList.vue';
import BasePagination from '@/components/base/BasePagination.vue';
import BaseLoader from '@/components/base/BaseLoader.vue';
import useProductsList from '@/hooks/useProductsList';
import ProductCounter from '@/components/product/ProductCounter.vue';
import wordEnding from '@/helpers/wordEnding';

export default defineComponent({
  components: {
    ProductList, BasePagination, ProductFilter, BaseLoader, ProductCounter,
  },

  setup() {
    const $route = useRoute();

    const filterPriceFrom = ref(0);
    const filterPriceTo = ref(0);
    const filterCategoryId = ref(Number($route.params.id) || 0);
    const filterColorId = ref([]);
    const filterMaterialId = ref([]);
    const filterSeasonId = ref([]);
    const page = ref(1);
    const productsPerPage = ref(12);
    const {
      products, fetchProducts, totalAmount, status: productStatus,
    } = useProductsList();
    const textToAmount = computed(() => wordEnding(totalAmount, ['товар', 'товара', 'товаров']));

    const loadProducts = () => fetchProducts(
      {
        page: page.value,
        limit: productsPerPage.value,
        categoryId: filterCategoryId.value,
        minPrice: filterPriceFrom.value,
        maxPrice: filterPriceTo.value,
        colorIds: filterColorId.value,
        materialIds: filterMaterialId.value,
        seasonIds: filterSeasonId.value,
      },
    );

    const watchedParams = [
      { name: page, func: loadProducts },
      { name: filterPriceFrom, func: loadProducts },
      { name: filterPriceTo, func: loadProducts },
      { name: filterCategoryId, func: loadProducts },
      { name: filterColorId, func: loadProducts },
      { name: filterMaterialId, func: loadProducts },
      { name: filterSeasonId, func: loadProducts },
      { name: productsPerPage, func: loadProducts },
    ];

    watchedParams.forEach((param) => {
      watch([param.name], () => {
        param.func();
      });
    });

    loadProducts();

    return {
      page,
      productsPerPage,

      filterPriceFrom,
      filterPriceTo,
      filterCategoryId,
      filterColorId,
      filterMaterialId,
      filterSeasonId,

      products,
      totalAmount,
      textToAmount,

      productStatus,
    };
  },

});
</script>
