export default function getRuColor(eng) {
  let ruColor;
  switch (eng.toLowerCase().trim()) {
    case 'white':
      ruColor = 'Белый';
      break;
    case 'black':
      ruColor = 'Чёрный';
      break;
    case 'red':
      ruColor = 'Красный';
      break;
    case 'blue':
      ruColor = 'Синий';
      break;
    case 'green':
      ruColor = 'Зелёный';
      break;
    case 'yellow':
      ruColor = 'Жёлтый';
      break;
    case 'purple':
      ruColor = 'Сиреневый';
      break;
    case 'pink':
      ruColor = 'Розовый';
      break;

    default:
      ruColor = eng;
      break;
  }
  return ruColor;
}
