<template>
  <ul class="tabs">
    <li
      v-for="tab in tabs"
      :key="tab.key"
      class="tabs__item"
    >
      <a
        :class="[
          'tabs__link',
          {
            'tabs__link--current': tab.key === selectedTabState.selectedTabKey,
          },
        ]"
        @click.prevent="selectedTabState.selectedTabKey = tab.key"
        @keydown="selectedTabState.selectedTabKey = tab.key"
      >
        {{ tab.title }}
      </a>
    </li>
  </ul>
  <div class="tab-content">
    <slot />
  </div>
</template>

<script>
import {
  ref, onMounted, provide, reactive,
} from 'vue';

export default {
  setup(props, { slots }) {
    const tabs = ref([]);
    const selectedTabState = reactive({ selectedTabKey: null });
    provide('selectedTabState', selectedTabState);
    onMounted(() => {
      // eslint-disable-next-line array-callback-return, consistent-return
      tabs.value = slots.default().map((i) => {
        if (i.props && i.props['tab-key']) {
          return {
            key: i.props['tab-key'],
            title: i.props.title,
          };
        }
      });
      selectedTabState.selectedTabKey = tabs.value[0] && tabs.value[0].key;
    });
    return { tabs, selectedTabState };
  },
};
</script>
